//
// alertify.scss
//

.alertify {
  .ajs-body {
    .ajs-content {
      .ajs-input {
        color: var(--#{$variable-prefix}gray-600);
        background-color: var(--#{$variable-prefix}card-bg);
        border: 1px solid var(--#{$variable-prefix}gray-300);
      }
    }
  }
}

.alertify {
  .ajs-footer {
    .ajs-buttons {
      .ajs-button {
        color: var(--#{$variable-prefix}gray-600);
      }
    }
  }
}

.alertify {
  .ajs-header {
    font-weight: $font-weight-medium;
  }
  .ajs-body {
    color: var(--#{$variable-prefix}gary-600);
  }
  .ajs-dialog {
    box-shadow: $box-shadow;
    background-color: $modal-content-bg;

    .ajs-header,
    .ajs-footer {
      background-color: $modal-content-bg;
      color: var(--#{$variable-prefix}gary-600);
      border-bottom: var(--#{$variable-prefix}gray-300) 1px solid;
      border-top: var(--#{$variable-prefix}gray-300) 1px solid;
    }
  }

  .ajs-footer {
    .ajs-buttons {
      .ajs-button {
        font-weight: $font-weight-medium;
        &.ajs-ok {
          color: $primary;
        }
      }

      &.ajs-primary {
        text-align: right;
      }
    }
  }

  .ajs-body .ajs-content .ajs-input:focus-visible {
    outline: none;
  }

  .ajs-commands {
    right: 4px;
    left: auto;
    margin: -14px 0 0 24px;
  }
}

.alertify-notifier {
  .ajs-message {
    background-color: $primary;
    border-color: $primary;
    color: $white;
    text-shadow: none !important;
    &.ajs-success {
      background-color: $success;
      border-color: $success;
    }
    &.ajs-error {
      background-color: $danger;
      border-color: $danger;
    }
    &.ajs-warning {
      background-color: $warning;
      border-color: $warning;
    }
  }

  &.ajs-right {
    right: 10px;
    left: auto;
    .ajs-message {
      right: -320px;
      left: auto;
      &.ajs-visible {
        right: 290px;
        left: auto;
      }
    }
  }

  &.ajs-left {
    left: 10px;
    right: auto;
    .ajs-message {
      left: -300px;
      right: auto;
      &.ajs-visible {
        left: 0;
        right: auto;
      }
    }
  }
}
