//
// _header.scss
//

#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: $header-bg;
  border-bottom: 1px solid $border-color;
  padding-top: 1.1em;
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: $header-height;
  padding: 0 $grid-gutter-width 0 0;

  .dropdown .show {
    &.header-item {
      background-color: var(--#{$variable-prefix}gray-100);
    }
  }
}

.navbar-brand-box {
  padding: 0 1.5rem;
  width: $navbar-brand-box-width;
  background: $sidebar-bg;
  border-right: 1px solid $sidebar-border-color;
  box-shadow: 0px 2px 2px $sidebar-bg;
}

.logo {
  line-height: 69px;
  color: inherit !important;

  .logo-sm {
    display: none;
  }
}

.logo-txt {
  font-weight: 700;
  font-size: 18px;
  vertical-align: middle;
  margin-left: 5px;
}

.logo-light {
  display: none;
}

body:not([data-sidebar-size="sm"]) {
  #vertical-menu-btn {
    margin-left: -52px;
    margin-right: 20px;
    @media (max-width: 991.98px) {
      margin-left: 0;
    }
  }
}

/* Search */

.app-search {
  padding: calc(#{$header-height - 40px} / 2) 0;
  position: relative;

  .form-control {
    border: $search-width solid $search-border-color;
    height: 40px;
    padding-left: 17px;
    padding-right: 50px;
    background-color: $topbar-search-bg;
    box-shadow: none;
  }
  .btn {
    position: absolute;
    right: 3px;
    top: 3px;
    height: 34px;
    padding: 0 10px;
  }
}

.layout-mode-light {
  display: none;
}

body[data-layout-mode="dark"] {
  .layout-mode-dark {
    display: none;
  }
  .layout-mode-light {
    display: inline-block;
  }
}

// Mega menu

.megamenu-list {
  li {
    position: relative;
    padding: 5px 0px;
    a {
      color: $dropdown-color;
    }
  }
}

@media (max-width: 992px) {
  .navbar-brand-box {
    width: auto;
  }

  .logo {
    span.logo-lg {
      display: none;
    }

    span.logo-sm {
      display: inline-block;
    }
  }
}

.page-content {
  padding: calc(#{$header-height} + #{$grid-gutter-width})
    calc(#{$grid-gutter-width} / 2) $footer-height
    calc(#{$grid-gutter-width} / 2);
}

.header-item {
  height: $header-height;
  box-shadow: none !important;
  color: $header-item-color;
  border: 0;
  border-radius: 0px;

  &:hover {
    color: $header-item-color;
  }
}

.header-profile-user {
  height: 36px;
  width: 36px;
  background-color: var(--#{$variable-prefix}gray-600);
  padding: 3px;
}

.noti-icon {
  i {
    font-size: 22px;
    color: $header-item-color;
  }

  .badge {
    position: absolute;
    top: 12px;
    right: 4px;
  }
}

.notification-item {
  .d-flex {
    padding: 0.75rem 1rem;

    &:hover {
      background-color: $dropdown-link-hover-bg;
    }
  }
}

// Dropdown with Icons
.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  color: $gray-600;

  img {
    height: 24px;
  }

  span {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &:hover {
    background-color: var(--#{$variable-prefix}gray-100);
  }
}

// Full Screen
.fullscreen-enable {
  [data-toggle="fullscreen"] {
    .bx-fullscreen::before {
      content: "\ea3f";
    }
  }
}

body[data-topbar="dark"] {
  #page-topbar {
    background-color: $header-dark-bg;
    //border-color: $gray-dark-300;
  }
  .navbar-brand-box {
    background-color: $header-dark-bg;
    border-color: $header-dark-bg;
    box-shadow: 0px 0px 2px $header-dark-bg;
    .logo {
      color: $white !important;
    }
  }
  .navbar-header {
    .dropdown .show {
      &.header-item {
        background-color: rgba($white, 0.05);
      }
    }

    .waves-effect .waves-ripple {
      background: rgba($white, 0.4);
    }
  }

  .header-item {
    color: $header-dark-item-color;

    &:hover {
      color: $header-dark-item-color;
    }

    &.border-start,
    &.border-end {
      border-color: rgba($white, 0.1) !important;
    }

    &.bg-soft-light {
      background-color: rgba($white, 0.04) !important;
    }
  }

  .header-profile-user {
    background-color: rgba($white, 0.25);
  }

  .noti-icon {
    i {
      color: $header-dark-item-color;
    }
  }

  .logo-dark {
    display: none;
  }

  .logo-light {
    display: block;
  }

  .app-search {
    .form-control {
      background-color: var(--#{$variable-prefix}topbar-dark-search-bg);
      color: var(--#{$variable-prefix}gray-500);
    }
    span,
    input.form-control::-webkit-input-placeholder {
      color: rgba($white, 0.5);
    }
  }
}

@media (max-width: 600px) {
  .navbar-header {
    .dropdown {
      position: static;

      .dropdown-menu {
        left: 10px !important;
        right: 10px !important;
      }
    }
  }
}

@media (max-width: 380px) {
  .navbar-brand-box {
    display: none;
  }
}

body[data-layout="horizontal"] {
  .navbar-brand-box {
    width: auto;
    border: 0;
    background-color: transparent;
    box-shadow: none;
    @media (min-width: 992px) {
      padding-left: 0;
      margin-right: 10px;
    }
  }
  .page-content {
    margin-top: $header-height;
    padding: calc(55px + #{$grid-gutter-width}) calc(#{$grid-gutter-width} / 2)
      $footer-height calc(#{$grid-gutter-width} / 2);
  }

  .navbar-header {
    @media (min-width: 992px) {
      padding-left: $grid-gutter-width;
      padding-right: $grid-gutter-width;
    }
  }

  &[data-sidebar="dark"] {
    .navbar-brand-box {
      background-color: transparent;
      box-shadow: none;
    }
  }
}

@media (max-width: 992px) {
  body[data-layout="horizontal"] {
    .page-content {
      margin-top: 15px;
    }
  }
}
