//
// colorpicker.scss
//

.pcr-app {
  background: $card-bg;
}

.pcr-app[data-theme="classic"] {
  .pcr-selection {
    .pcr-color-preview {
      margin-right: 0.75em;
      margin-left: 0;
    }

    .pcr-color-chooser,
    .pcr-color-opacity {
      margin-left: 0.75em;
      margin-right: 0;
    }
  }
}
