// Font weight help class

.fw-medium {
  font-weight: $font-weight-medium;
}

.fw-semibold {
  font-weight: $font-weight-semibold;
}

// Icons Sizes
.icon-xs {
  height: 14px;
  width: 14px;
}

.icon-sm {
  height: 16px;
  width: 16px;
}

.icon-md {
  height: 18px;
  width: 18px;
}

.icon-lg {
  height: 20px;
  width: 20px;
}

.icon-xl {
  height: 22px;
  width: 22px;
}

// Card Height 100
.card-h-100 {
  height: calc(100% - #{$grid-gutter-width});
}

// Social

.social-list-item {
  height: 2rem;
  width: 2rem;
  line-height: calc(2rem - 4px);
  display: block;
  border: 2px solid $gray-500;
  border-radius: 50%;
  color: $gray-500;
  text-align: center;
  transition: all 0.4s;

  &:hover {
    color: $gray-600;
    background-color: $gray-200;
  }
}

.w-xs {
  min-width: 80px;
}

.w-sm {
  min-width: 95px;
}

.w-md {
  min-width: 110px;
}

.w-lg {
  min-width: 140px;
}

.w-xl {
  min-width: 160px;
}

// bg overlay
.bg-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  opacity: 0.7;
  background-color: $black;
}

// alert

.alert-dismissible {
  .btn-close {
    font-size: 10px;
    padding: $alert-padding-y * 1.4 $alert-padding-x;
    background: transparent escape-svg($btn-close-bg) center / $btn-close-width
      auto no-repeat;
  }
}

.btn-group-vertical {
  label {
    margin-bottom: 0;
  }
}

.btn-group {
  label {
    margin-bottom: 0;
  }
}

[data-layout-mode="dark"] {
  .text-light {
    color: var(--#{$variable-prefix}gray-600) !important;
  }
  .text-body {
    color: var(--#{$variable-prefix}gray-400) !important;
  }
  .btn-close {
    filter: invert(1) grayscale(100%) brightness(200%);
  }
  .btn-close-white {
    filter: invert(0) grayscale(100%) brightness(200%);
  }
}
