//
// Form editors.scss
//

// CK Editor

// ck-editor

.ck-editor__editable {
  min-height: 250px !important;
}

.ck {
  &.ck-reset_all,
  &.ck-reset_all * {
    color: var(--#{$variable-prefix}gray-700) !important;
  }

  &.ck-editor__main > .ck-editor__editable:not(.ck-focused) {
    border-color: $input-border-color !important;
  }

  &.ck-toolbar {
    background-color: var(--#{$variable-prefix}light) !important;
    border-color: $input-border-color !important;

    .ck.ck-toolbar__separator {
      background-color: $input-border-color !important;
    }
  }

  &.ck-tooltip .ck-tooltip__text {
    background-color: $dark !important;
    color: $light !important;
  }

  &.ck-editor__main > .ck-editor__editable,
  &.ck-list {
    background-color: $card-bg !important;
  }

  &.ck-dropdown__panel {
    border-color: $border-color !important;
  }
}

.ck.ck-button:not(.ck-disabled):hover,
a.ck.ck-button:not(.ck-disabled):hover,
.ck.ck-button.ck-on,
a.ck.ck-button.ck-on {
  background-color: var(--#{$variable-prefix}light) !important;
}

[dir="rtl"] {
  .ck.ck-toolbar > .ck-toolbar__items {
    flex-direction: row-reverse;
  }
}
